'use client';
import React, { useState } from 'react';

export default function SettingsPage() {
  // Store Settings
  const [storeName, setStoreName] = useState('My Store');
  const [storeEmail, setStoreEmail] = useState('store@example.com');
  const [storeCurrency, setStoreCurrency] = useState('USD');
  
  // Payment Settings
  const [paymentMethod, setPaymentMethod] = useState('Credit Card');
  
  // Shipping Settings
  const [shippingMethod, setShippingMethod] = useState('Standard');
  const [shippingRate, setShippingRate] = useState('Flat Rate');
  
  // Tax Settings
  const [taxRate, setTaxRate] = useState(5);

  // Order and Fulfillment Settings
  const [orderWorkflow, setOrderWorkflow] = useState('Pending > Processing > Shipped > Completed');

  // User Roles and Permissions
  const [adminRole, setAdminRole] = useState(true);

  // Security Settings
  const [password, setPassword] = useState('');
  const [dataProtection, setDataProtection] = useState(true);

  const handleSubmit = (e: React.FormEvent, section: string) => {
    e.preventDefault();
    alert(`${section} settings saved successfully!`);
  };

  const handleReset = (section: string) => {
    if (section === 'Store') {
      setStoreName('My Store');
      setStoreEmail('store@example.com');
      setStoreCurrency('USD');
    } else if (section === 'Payment') {
      setPaymentMethod('Credit Card');
    } else if (section === 'Shipping') {
      setShippingMethod('Standard');
      setShippingRate('Flat Rate');
    } else if (section === 'Tax') {
      setTaxRate(5);
    } else if (section === 'Order') {
      setOrderWorkflow('Pending > Processing > Shipped > Completed');
    } else if (section === 'Role') {
      setAdminRole(true);
    } else if (section === 'Security') {
      setPassword('');
      setDataProtection(true);
    }
    alert(`${section} settings reset to default.`);
  };

  return (
    <section className="p-6 bg-gray-50 rounded-lg shadow-lg">
      <h2 className="text-2xl font-semibold text-gray-800 mb-6">Settings</h2>

      {/* Store Settings */}
      <div className="mb-8">
        <h3 className="text-xl font-semibold text-gray-800 mb-4">Store Settings</h3>
        <form onSubmit={(e) => handleSubmit(e, 'Store')} className="space-y-6">
          <div>
            <label className="block text-gray-700 font-medium">Store Name</label>
            <input
              type="text"
              value={storeName}
              onChange={(e) => setStoreName(e.target.value)}
              className="w-full px-4 py-2 border rounded-lg focus:outline-none focus:ring-2 focus:ring-indigo-500"
            />
          </div>

          <div>
            <label className="block text-gray-700 font-medium">Store Email</label>
            <input
              type="email"
              value={storeEmail}
              onChange={(e) => setStoreEmail(e.target.value)}
              className="w-full px-4 py-2 border rounded-lg focus:outline-none focus:ring-2 focus:ring-indigo-500"
            />
          </div>

          <div>
            <label className="block text-gray-700 font-medium">Currency</label>
            <select
              value={storeCurrency}
              onChange={(e) => setStoreCurrency(e.target.value)}
              className="w-full px-4 py-2 border rounded-lg focus:outline-none focus:ring-2 focus:ring-indigo-500"
            >
              <option value="USD">USD</option>
              <option value="EUR">EUR</option>
              <option value="GBP">GBP</option>
            </select>
          </div>

          <div className="flex justify-between">
            <button
              type="submit"
              className="px-6 py-3 bg-blue-600 text-white rounded-lg hover:bg-blue-700 transition-colors"
            >
              Save Store Settings
            </button>
            <button
              type="button"
              onClick={() => handleReset('Store')}
              className="px-6 py-3 bg-gray-600 text-white rounded-lg hover:bg-gray-700 transition-colors"
            >
              Reset Store Settings
            </button>
          </div>
        </form>
      </div>

      {/* Payment Settings */}
      <div className="mb-8">
        <h3 className="text-xl font-semibold text-gray-800 mb-4">Payment Settings</h3>
        <form onSubmit={(e) => handleSubmit(e, 'Payment')} className="space-y-6">
          <div>
            <label className="block text-gray-700 font-medium">Payment Method</label>
            <select
              value={paymentMethod}
              onChange={(e) => setPaymentMethod(e.target.value)}
              className="w-full px-4 py-2 border rounded-lg focus:outline-none focus:ring-2 focus:ring-indigo-500"
            >
              <option value="Credit Card">Credit Card</option>
              <option value="PayPal">PayPal</option>
              <option value="Bank Transfer">Bank Transfer</option>
            </select>
          </div>

          <div className="flex justify-between">
            <button
              type="submit"
              className="px-6 py-3 bg-blue-600 text-white rounded-lg hover:bg-blue-700 transition-colors"
            >
              Save Payment Settings
            </button>
            <button
              type="button"
              onClick={() => handleReset('Payment')}
              className="px-6 py-3 bg-gray-600 text-white rounded-lg hover:bg-gray-700 transition-colors"
            >
              Reset Payment Settings
            </button>
          </div>
        </form>
      </div>

      {/* Shipping Settings */}
      <div className="mb-8">
        <h3 className="text-xl font-semibold text-gray-800 mb-4">Shipping Settings</h3>
        <form onSubmit={(e) => handleSubmit(e, 'Shipping')} className="space-y-6">
          <div>
            <label className="block text-gray-700 font-medium">Shipping Method</label>
            <select
              value={shippingMethod}
              onChange={(e) => setShippingMethod(e.target.value)}
              className="w-full px-4 py-2 border rounded-lg focus:outline-none focus:ring-2 focus:ring-indigo-500"
            >
              <option value="Standard">Standard</option>
              <option value="Express">Express</option>
              <option value="International">International</option>
            </select>
          </div>

          <div>
            <label className="block text-gray-700 font-medium">Shipping Rate</label>
            <select
              value={shippingRate}
              onChange={(e) => setShippingRate(e.target.value)}
              className="w-full px-4 py-2 border rounded-lg focus:outline-none focus:ring-2 focus:ring-indigo-500"
            >
              <option value="Flat Rate">Flat Rate</option>
              <option value="Per Item">Per Item</option>
              <option value="Weight-based">Weight-based</option>
            </select>
          </div>

          <div className="flex justify-between">
            <button
              type="submit"
              className="px-6 py-3 bg-blue-600 text-white rounded-lg hover:bg-blue-700 transition-colors"
            >
              Save Shipping Settings
            </button>
            <button
              type="button"
              onClick={() => handleReset('Shipping')}
              className="px-6 py-3 bg-gray-600 text-white rounded-lg hover:bg-gray-700 transition-colors"
            >
              Reset Shipping Settings
            </button>
          </div>
        </form>
      </div>

      {/* Tax Settings */}
      <div className="mb-8">
        <h3 className="text-xl font-semibold text-gray-800 mb-4">Tax Settings</h3>
        <form onSubmit={(e) => handleSubmit(e, 'Tax')} className="space-y-6">
          <div>
            <label className="block text-gray-700 font-medium">Tax Rate (%)</label>
            <input
              type="number"
              value={taxRate}
              onChange={(e) => setTaxRate(Number(e.target.value))}
              className="w-full px-4 py-2 border rounded-lg focus:outline-none focus:ring-2 focus:ring-indigo-500"
            />
          </div>

          <div className="flex justify-between">
            <button
              type="submit"
              className="px-6 py-3 bg-blue-600 text-white rounded-lg hover:bg-blue-700 transition-colors"
            >
              Save Tax Settings
            </button>
            <button
              type="button"
              onClick={() => handleReset('Tax')}
              className="px-6 py-3 bg-gray-600 text-white rounded-lg hover:bg-gray-700 transition-colors"
            >
              Reset Tax Settings
            </button>
          </div>
        </form>
      </div>

      {/* Order and Fulfillment Settings */}
      <div className="mb-8">
        <h3 className="text-xl font-semibold text-gray-800 mb-4">Order and Fulfillment Settings</h3>
        <form onSubmit={(e) => handleSubmit(e, 'Order')} className="space-y-6">
          <div>
            <label className="block text-gray-700 font-medium">Order Workflow</label>
            <textarea
              value={orderWorkflow}
              onChange={(e) => setOrderWorkflow(e.target.value)}
              className="w-full px-4 py-2 border rounded-lg focus:outline-none focus:ring-2 focus:ring-indigo-500"
              rows={4}
            />
          </div>

          <div className="flex justify-between">
            <button
              type="submit"
              className="px-6 py-3 bg-blue-600 text-white rounded-lg hover:bg-blue-700 transition-colors"
            >
              Save Order Settings
            </button>
            <button
              type="button"
              onClick={() => handleReset('Order')}
              className="px-6 py-3 bg-gray-600 text-white rounded-lg hover:bg-gray-700 transition-colors"
            >
              Reset Order Settings
            </button>
          </div>
        </form>
      </div>

      {/* User Roles and Permissions */}
      <div className="mb-8">
        <h3 className="text-xl font-semibold text-gray-800 mb-4">User Roles and Permissions</h3>
        <form onSubmit={(e) => handleSubmit(e, 'Role')} className="space-y-6">
          <div className="flex items-center">
            <input
              type="checkbox"
              checked={adminRole}
              onChange={(e) => setAdminRole(e.target.checked)}
              className="h-5 w-5 text-indigo-500 focus:ring-indigo-500 border-gray-300 rounded"
            />
            <label className="ml-2 text-gray-700">Admin Role</label>
          </div>

          <div className="flex justify-between">
            <button
              type="submit"
              className="px-6 py-3 bg-blue-600 text-white rounded-lg hover:bg-blue-700 transition-colors"
            >
              Save Role Settings
            </button>
            <button
              type="button"
              onClick={() => handleReset('Role')}
              className="px-6 py-3 bg-gray-600 text-white rounded-lg hover:bg-gray-700 transition-colors"
            >
              Reset Role Settings
            </button>
          </div>
        </form>
      </div>

      {/* Security and Compliance Settings */}
      <div className="mb-8">
        <h3 className="text-xl font-semibold text-gray-800 mb-4">Security and Compliance</h3>
        <form onSubmit={(e) => handleSubmit(e, 'Security')} className="space-y-6">
          <div>
            <label className="block text-gray-700 font-medium">Password</label>
            <input
              type="password"
              value={password}
              onChange={(e) => setPassword(e.target.value)}
              className="w-full px-4 py-2 border rounded-lg focus:outline-none focus:ring-2 focus:ring-indigo-500"
            />
          </div>

          <div className="flex items-center">
            <input
              type="checkbox"
              checked={dataProtection}
              onChange={(e) => setDataProtection(e.target.checked)}
              className="h-5 w-5 text-indigo-500 focus:ring-indigo-500 border-gray-300 rounded"
            />
            <label className="ml-2 text-gray-700">Enable Data Protection</label>
          </div>

          <div className="flex justify-between">
            <button
              type="submit"
              className="px-6 py-3 bg-blue-600 text-white rounded-lg hover:bg-blue-700 transition-colors"
            >
              Save Security Settings
            </button>
            <button
              type="button"
              onClick={() => handleReset('Security')}
              className="px-6 py-3 bg-gray-600 text-white rounded-lg hover:bg-gray-700 transition-colors"
            >
              Reset Security Settings
            </button>
          </div>
        </form>
      </div>
    </section>
  );
}
